<template>
  <el-container>
    <el-header>
      <header-buttons v-if="mainPage.show" :showRefresh="true"  @buttonClickEvent="buttonClickEvent" cssType="1"></header-buttons>
      <HeaderTitle v-if="historyStatusInfoPage.show || currentStatusInfoPage.show"  @searchButtonClick="showSearchCard" :flag="mainPage.show"
      :titleName='titleName'>
        <div slot="head-operation" class="head-operation">
          <HeadOperationItem
            name="addDevice"
            @onOprationItem="closeCurrentStatusInfo"
            v-if="historyStatusInfoPage.show"
          >
            <i slot="operation-icon" class="el-icon-caret-left"></i>
            <span slot="operation-name">{{ BTN_TEXT.RETURN }}</span>
          </HeadOperationItem>
          <HeadOperationItem
            name="addDevice"
            @onOprationItem="closeHistoryStatusInfo"
            v-if="currentStatusInfoPage.show"
          >
            <i slot="operation-icon" class="el-icon-caret-left"></i>
            <span slot="operation-name">{{ BTN_TEXT.RETURN }}</span>
          </HeadOperationItem>
          <!-- <HeadOperationItem
            name="showInfo"
            @onOprationItem="exportTable()"
            v-if="mainPage.show"
          >
            <i slot="operation-icon" class="icon-export"></i>
            <span slot="operation-name">{{ BTN_TEXT.EXPORT }}</span>
          </HeadOperationItem>
          <HeadOperationItem
            name="showInfo"
            @onOprationItem="findList()"
            v-if="mainPage.show"
          >
            <i slot="operation-icon" class="icon-refresh"></i>
            <span slot="operation-name">{{ BTN_TEXT.REFRESH }}</span>
          </HeadOperationItem> -->
        </div>
      </HeaderTitle>
    </el-header>
    <header-search
        v-if="mainPage.show"
        :show-primary-search="true"
        @refreshTableData="refreshTableData"
        cssType="1"
      >
    </header-search>

    <el-main class="view eq" style='overflow-y:visible !important;'>
      <div  v-if="mainPage.show" class='search-fixed' >
        <div>
          <span v-if='activeTab===1' >时间范围</span>
          <el-date-picker
            v-if='activeTab===1'
            class="dateTime"
            v-model="dateTime"
            type="datetimerange"
            align="left"
            size="mini"
            unlink-panels
            value-format="yyyy-MM-dd HH:mm:ss"
            :range-separator="BTN_TEXT.TO"
            :start-placeholder="BTN_TEXT.STARTTIME"
            :end-placeholder="BTN_TEXT.ENDTIME"
            :default-time="['00:00:00', '23:59:59']"
            :picker-options='pickerOptions'
          ></el-date-picker>
          <!-- <span>班次范围</span>
          <el-select v-model="shiftData"  size="mini" placeholder="请选择">
            <el-option
              v-for="item in shiftOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select> -->
          <el-button v-if='activeTab===1' type="default" style="margin-left:5px;"  size="mini" @click="searchShift()" >查询</el-button>
        </div>
      </div>
      <!-- 搜索框 -->
     <!--  <search-box
        ref="searchBox"
        v-show="isSearchBoxShow"
        @searchBtnClick="complexSearch"
        @searchReset="reset"
        :searchList="searchList"
      >
      </search-box> -->

      <!-- main table -->
      <el-row class="tableShow" v-if="mainPage.show">
        <div id="tabs">
          <ul>
            <li
              v-for="(tab, index) in tabs"
              @click="toggle(index, tab.state)"
              :class="{ active: activeTab == index }"
              :key="index"
              v-show="tab.showFlag"
            >
              {{ tab.name }}
            </li>
          </ul>
        </div>
        <el-col :span="24">
          <VXETable
            ref="xGrid"
            :tableName="''"
            :height="$options.filters.getTableHeight(260)"
            :tableLoading="mainPage.tableLoading"
            :tableDatas="mainPage.data"
            :tableColumns="mainPage.columns"
            :highlightCurrentRow="true"
            :tablePage="mainPage.page"
            @sortChange="sortChange"
            @onTablePageChange="handleMainPageChange"
          >
          </VXETable>
        </el-col>
      </el-row>
      <HistoryInfo v-if='historyStatusInfoPage.show' :allData='historyStatusInfoPage.tableData'></HistoryInfo>
      <CurrentInfo
        v-if='currentStatusInfoPage.show'
        :deviceCode='currentStatusInfoPage.deviceCode'
        :barEchartData="currentStatusInfoPage.propertyData"
        :sequenceChartData="currentStatusInfoPage.sequenceChartData"
      ></CurrentInfo>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/report/EquipmentStatus_andon/index.js';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/report/EquipmentStatus/index.less';
</style>
