// import { CommonService } from '@/services/common.service';
// 引入table组件
import VXETable from '@/components/common/Table';
const stateEnum = {
  red_e: '异常',
  blue_m: '保养',
  green_w: '待料',
  yellow_off: '停用',
  white_m: '维护',
  green_p: '生产'
};
export default {
  components: {
    VXETable
  },
  data() {
    return {
      tableLoading: false,
      tableDatas: [],
      tableColumns: [
        {
          field: 'startTime',
          title: '开始时间',
          align: 'center',
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss']
        },
        {
          field: 'endTime',
          title: '结束时间1',
          align: 'center',
          formatter: 'formatDate'
        },
        {
          field: 'time',
          title: '持续时间',
          align: 'center',
          formatter: 'timeStampSeconds'
        },
        {
          field: 'lightState',
          title: '状态',
          align: 'center',
          formatter: ({ cellValue }) => {
            return stateEnum[cellValue];
          }
        }
      ],
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevPage',
          'Number',
          'NextPage',
          'Total'
        ],
        perfect: true
      }
    };
  },
  props: [
    'allData'
  ],
  methods: {
    onTablePageChange(currentPage, pageSize) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.tableDatas = this.allData.slice(pageSize * (currentPage - 1), pageSize * currentPage);
      console.log(this.tableDatas);
    }
  },
  created() {
    this.tablePage.total = this.allData.length;
    this.onTablePageChange(1, 10);
    console.log(1112);
    console.log(this.allData);
  }
};
