import { showErrorNotify } from '@/utils/notify';
import { showWarningMessage } from '@/utils/message';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// import { CommonService } from '@/services/common.service';
// 引入table组件
import VXETable from '@/components/common/Table';

import { GetEfficiencyTrend, GetCycleTrend, GetStatus24Hour } from '@/api/statusEquipmentReport';
import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  PAGE_TITLE,
  ENERGY,
  MAINTAINTASK_STATUS,
  EQUIPMENT_STATUS_REPORT,
  // STATUS_CODE,
  TITLE,
  MSG,

  STATUS_CODE
} from '@/utils/constantsFile';

export default {

  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable
  },

  data() {
    return {
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      COLOR: EQUIPMENT_STATUS_REPORT.COLOR,
      activeTab: 0,
      date: this.moment(new Date()).format('YYYY-MM-DD') + ' 00:00:00',
      pickerOption: {
        disabledDate: (current) => {
          return current.getTime() > new Date().getTime();
        }
      },
      tabs: [
        {
          type: 'current',
          name: '柱状图',
          state: 0,
          showFlag: true
        },
        {
          type: 'history',
          name: '比例图',
          state: 1,
          showFlag: true
        }
      ],
      propertyData: [],
      // 状态时间图数据
      timePieData: {},
      efficiencyData: {
        label: [

        ],
        data: [

        ]
      },
      cycleData: {
        data: [

        ],
        label: [

        ]
      },
      propertyLoading: false,
      efficiencyLoading: false,
      cycleLoading: false,
      stateEnum: {
        deactivateValue: '停用',
        exceptionValue: '异常',
        maintainValue: '保养',
        materialWaitingValue: '待料',
        preserveValue: '维护',
        productValue: '生产'
      }
    };
  },
  computed: {
    tabTitle: function() {
      return this.tabs[this.activeTab].name;
    }
  },
  props: ['deviceCode', 'barEchartData', 'sequenceChartData'],
  watch: {
    activeTab: function() {
      this.drawProperty();
    },
    propertyData: function() {
      this.drawProperty();
    },
    timePieData: function() {
      // this.drawStackPie();
    },
    efficiencyData: function() {
      this.drawLine(this.$refs.efficiencyLine, '效率（%）', '时间（小时）', this.efficiencyData);
    },
    cycleData: function() {
      this.drawLine(this.$refs.cycleLine, '周期（秒）', '时间（小时）', this.cycleData);
    }
  },
  methods: {
    toggle(index, state) {
      this.activeTab = state;
    },
    /**
     * @description: 状态时钟图数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getTimeData() {
      try {
        this.clockLoading = true;
        const data = await GetStatus24Hour({
          deviceCode: this.deviceCode,
          time: this.date
        });
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.timePieData = {
            dayCount: data.rows.cumulativeProduction,
            stackData: [data.rows.runTimeSum, data.rows.idleTimeSum, data.rows.offlineTimeSum, data.rows.warnTimeSum],
            rows: data.rows.timeList.map(el => ({
              name: el.state,
              value: el.time
            }))
          };
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.clockLoading = false;
      }
    },
    /**
     * @description: 获取效率趋势图数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getEfficiencyData() {
      try {
        const endTime = `${this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')}`;
        const startTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`;
        this.efficiencyLoading = true;
        const data = await GetEfficiencyTrend(
          {
            deviceCode: this.deviceCode,
            startTime,
            endTime
          }
        );

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.efficiencyData = {
            label: data.rows.map(el => el.time),
            data: data.rows.map(el => el.value)
          };
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.efficiencyLoading = false;
      }
    },
    /**
     * @description: 获取周期趋势图数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getCycleData() {
      try {
        this.cycleLoading = true;
        const endTime = `${this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')}`;
        const startTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`;

        const data = await GetCycleTrend(
          {
            deviceCode: this.deviceCode,
            startTime,
            endTime
          }
        );

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.cycleData = {
            label: data.rows.map(el => el.time),
            data: data.rows.map(el => el.value)
          };
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.cycleLoading = false;
      }
    },
    /**
     * @description: 绘制折线图
     * @param { ref } elem
     * @param {*} yLabel
     * @param {*} xLabel
     * @param {*} dataSource {label:[],data:[]}
     * @return {*}
     * @author: zhxy
     */
    drawLine(elem, yLabel, xLabel, dataSource) {
      const lineChart = this.$echarts.init(elem);
      const option = {
        color: ['rgb(27, 137, 247)'],
        tooltip: {
          trigger: 'axis',
          formatter: function(param) {
            return `${xLabel.slice(0, 2)}:${param[0].name}<br/>${yLabel.slice(0, 2)}:${param[0].data}`;
          }
        },
        xAxis: {
          type: 'category',
          // boundaryGap: false,
          data: dataSource.label.map(el => el.slice(-8)),

          name: xLabel,
          axisTick: {
            alignWithLabel: true
          }
        },
        yAxis: {
          type: 'value',

          name: yLabel

        },
        series: [{
          type: 'line',
          data: dataSource.data,
          // showSymbol: false,
          symbol: 'circle',
          symbolSize: 0.01,
          label: {
            normal: {
              show: true,
              position: 'outside'
            }
          }
        }]
      };
      lineChart.setOption(option, true);
      window.addEventListener('resize', () => {
        lineChart.resize();
      });
    },
    /**
     * @description: 切换柱状图或比例图（饼图）
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawProperty() {
      if (this.activeTab === 0) {
        this.drawPropertyBar();
      } else {
        this.drawPropertyPie();
      }
    },
    /**
     * @description: 绘制状态柱状图
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawPropertyBar() {
      const barChart = this.$echarts.init(this.$refs.statusBarChart);
      const colorEunm = {
        deactivateValue: '#FFD307',
        exceptionValue: '#FC4A45',
        maintainValue: '#07aaff',
        materialWaitingValue: '#00E62C',
        preserveValue: '#FF6B22',
        productValue: '#007E2E'
      };
      const seriesData = [];
      for (const key in this.stateEnum) {
        seriesData.push({
          name: this.stateEnum[key],
          value: this.barEchartData[key].toFixed(2),
          itemStyle: {
            color: colorEunm[key]
          }
        });
      }
      const option = {
        color: EQUIPMENT_STATUS_REPORT.COLOR,
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        barMaxWidth: '60%',
        grid: {
          top: '9%',
          left: '3%',
          right: '6%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: ['停用', '异常', '保养', '待料', '维护', '生产'],
          axisTick: {
            alignWithLabel: true
          },
          name: '状态',
          nameGap: '5'
        }],
        yAxis: [{
          type: 'value',
          name: '时间/h',
          splitLine: {
            show: false
          }
        }],
        series: [
          {
            type: 'bar',
            label: {
              show: true,
              position: 'top',
              color: 'auto'
            },
            data: seriesData
          }
        ]
      };
      barChart.setOption(option, true);
      window.addEventListener('resize', () => {
        barChart.resize();
      });
    },
    /**
     * @description: 绘制状态比例图
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawPropertyPie() {
      const pieChart = this.$echarts.init(this.$refs.statusBarChart);
      const colorEunm = {
        deactivateValue: '#5a6167',
        exceptionValue: '#c74746',
        maintainValue: '#07aaff',
        materialWaitingValue: '#228ffe',
        preserveValue: '#f16804',
        productValue: '#027cf9'
      };
      const seriesData = [];
      const totalTime = this.barEchartData.totalValue.toFixed(2);
      for (const key in this.stateEnum) {
        seriesData.push({
          name: this.stateEnum[key],
          value: this.barEchartData[key].toFixed(2),
          itemStyle: {
            color: colorEunm[key]
          }
        });
      }
      const option = {
        tooltip: {
          trigger: 'item'
        },
        series: [
          {
            type: 'pie',
            name: '',
            label: {
              show: true,
              position: 'center',
              formatter: () => {
                // const totalTime = this.propertyData.reduce((a, b) => (a + b), 0).toFixed(2);
                return `{a|${totalTime}}\n{b|共计（小时）}`;
              },
              rich: {
                a: {
                  color: '#c74746',
                  fontSize: 36
                },
                b: {
                  color: '#c74746'
                }
              }
            },
            tooltip: {
              formatter: '{b}:{d}%'
            },
            radius: ['50%', '70%'],
            center: ['50%', '50%'],
            data: seriesData
          }
        ]
      };
      pieChart.setOption(option, true);
      window.addEventListener('resize', () => {
        pieChart.resize();
      });
    },
    /**
     * @description: 运行时序图数据处理
     * @param {type}
     * @return:
     */
    dealSequenceData() {
      const seriesData = [];
      const colorEunm = {
        yellow_off: '#F5CA00',
        red_e: '#FF0700',
        blue_m: '#07aaff',
        green_w: '#00E62C',
        white_m: '#FF6B22',
        green_p: '#007E2E'
      };
      const indexEunm = {
        yellow_off: 0,
        red_e: 1,
        blue_m: 2,
        green_w: 3,
        white_m: 4,
        green_p: 5
      };
      const stateEnum = {
        yellow_off: '停用',
        red_e: '异常',
        blue_m: '保养',
        green_w: '待料',
        white_m: '维护',
        green_p: '生产'
      };
      const temp = this.sequenceChartData.reduce((res, item) => {
        res[item.lightState] ? res[item.lightState].push({ name: item.lightState, value: [indexEunm[item.lightState], item.startTime, item.endTime] }) : res[item.lightState] = [{ name: item.lightState, value: [indexEunm[item.lightState], item.startTime, item.endTime] }];
        return res;
      }, {});
      for (const key in stateEnum) {
        seriesData.push({
          name: stateEnum[key],
          type: 'custom',
          renderItem: this.renderRect,
          color: colorEunm[key],
          encode: {
            x: [1, 2],
            y: 0
          },
          data: temp[key]
        });
      };
      this.showSequenceChart(seriesData);
    },
    /**
     * @description: 运行时序图
     * @param {type}
     * @return:
     */
    showSequenceChart(seriesData) {
      console.log(seriesData);
      const myChart = this.$echarts.init(document.getElementById('sequenceChart'));
      myChart.setOption({
        tooltip: {
          formatter: (params) => {
            const start = params.value[1];
            const end = params.value[2];
            const interval = (+new Date(end) - +new Date(start)) / 1000;
            return `${params.marker}${params.seriesName}:${start.substring(11, 16)} - ${end.substring(11, 16)}(${this.timeStampSeconds(interval)})`;
          }
        },
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            top: 'bottom',
            show: false
          },
          {
            type: 'inside',
            filterMode: 'weakFilter',
            show: false
          }
        ],
        grid: {
          containLabel: true,
          top: '10%',
          bottom: '2%',
          left: '2%',
          right: '8%'
        },
        xAxis: {
          name: '时间',
          type: 'time',
          axisLabel: {
            formatter: (value, index) => this.moment(value).format('HH:mm')
          }
        },
        yAxis: {
          name: '状态',
          data: seriesData.map(item => item.name)
        },
        series: seriesData
      });
    },
    renderRect(params, api) {
      const index = api.value(0);
      const start = api.coord([api.value(1), index]);
      const end = api.coord([api.value(2), index]);
      const height = Math.min(api.size([0, 1])[1], 50);
      // const height = 300;
      const rectShape = this.$echarts.graphic.clipRectByRect({
        x: start[0],
        y: start[1] - height / 2,
        // y: height + (index * 30),
        width: end[0] - start[0],
        height: height
      }, {
        x: params.coordSys.x,
        y: params.coordSys.y,
        width: params.coordSys.width,
        height: params.coordSys.height
      });
      return (
        rectShape && {
          type: 'rect',
          transition: ['shape'],
          shape: rectShape,
          style: api.style()
        }
      );
    },
    timeStampSeconds(cellValue) {
      var day = parseInt(cellValue / 60 / 60 / 24);
      var hour = parseInt(cellValue / 60 / 60 % 24);
      var min = parseInt(cellValue / 60 % 60);
      var s = parseInt(cellValue % 60);
      cellValue = '';
      if (day > 0) {
        cellValue = day + '天';
      }
      if (hour > 0) {
        cellValue += hour + '小时';
      }
      if (min > 0) {
        cellValue += parseFloat(min) + '分钟';
      }
      if (s > 0) {
        cellValue += parseFloat(s) + '秒';
      }
      return cellValue;
    },
    init() {
      this.getTimeData().then(() => {
        // 更新柱状图/比例图数据
        this.propertyData = this.timePieData?.stackData?.map(el => Number((el / 60).toFixed(2))) || [];
      });

      this.getEfficiencyData();
      this.getCycleData();
      this.dealSequenceData();
    }
  },
  mounted() {
    this.init();
  }
};
